import React, {useState, Component} from "react";
import { Link } from "react-router-dom";
import "../styles/navBar.css"
import { motion } from '../../node_modules/framer-motion/dist/framer-motion';

import { navAnimation } from "../components_items/Animations.jsx";
import { NavMenuItems } from "../components_items/NavMenuItems";
import Logo from '../images/logo_pisan.png';


function  NavBar (){
    const [color, setColor] = useState(false)
    const [lastScrollY, setLastScrollY] = useState(0);
    const changeColor = () =>{
        if (window.scrollY === 0){
            setColor(false)
        }
        else if(window.scrollY > lastScrollY) {
            setColor(false)        
        } else{
            setColor(true)
        }
        setLastScrollY(window.scrollY); 
    }

    window.addEventListener('scroll', changeColor)

    const [navMenu, setNavMenu] = useState(false)
    const showNavBar = () => setNavMenu(!navMenu)
    

        return (  
            <motion.nav variants={navAnimation} initial="hidden" animate="show" className={color ? 'nav-group nav-bg' : 'nav-group'}>
                <div className="navbar-logo">
                    <img src={Logo} alt=""/>
                </div>

                <div className="menu-icons" onClick={showNavBar} >
                    <i className={navMenu ? "fas fa-times" : "fas fa-bars"}></i>
                </div>

                <ul className={navMenu ? "nav-menu active" : "nav-menu "}>
                    {NavMenuItems.map((item, index) =>{
                        return(
                            <li key={index}>
                                <Link className={item.cName} to={item.url}  onClick={() => {
                                    window.scroll({
                                    top: 0,
                                    left: 0,
                                    behavior: "smooth",
                                    });
                                }}>{item.title}</Link>
                            </li>
                        )
                    })}
                        
                </ul>
            </motion.nav>
        );
    
}

export default NavBar;

